<template>
  <div>
    <div class="calendar">

    </div>
  </div>
</template>
<script>
import calendar from "@/assets/js/calendar.js";
export default {
  data() {
    return {
      objdata: {}
    };
  },
  methods: {},
  props: ["Calendardata"],
  mounted() {
    var _this = this;
    new calendar({
      selectors: {
        sections: ".calendar"
      },
      index: 5, //展示的月份个数
      animateFunction: "toggle", //动画效果
      controlDay: true, //知否控制在daysnumber天之内，这个数值的设置前提是总显示天数大于90天
      daysnumber: "120", //控制天数
      comeColor: "#4499ff", //入住颜色
      outColor: "#4499ff", //离店颜色
      comeoutColor: "#d1e5ff", //入住和离店之间的颜色
      callback: function(data) {
        // this.Calendardata = abc;
        _this.$emit("update:Calendardata", data);
      }, //回调函数
      comfireBtn: ".comfire" //确定按钮的class或者id
    });
  }
};
</script>
<style  lang='less' rel='stylesheet/less'>
.calendar {
  width: 100%;
  z-index: 999999999;
  top: 0;
  padding: 0.56rem 0 1.2rem 0;
  overflow: auto;
  height: 100%;
  background: #fff;
  font-size: 0.28rem;
  .dateZone {
    width: 100%;
    position: absolute;
    top: 0.9rem;
    left: 0;
    color: #666;
    background: #f4f4f4;
    text-align: center;
    td {
      width: 14.2%;
      padding: 0;
      height: 0.56rem;
      line-height: 0.56rem;
    }
    .colo {
      color: #fa4950;
    }
  }
  .tbody {
    background: #fff;
    .ny1 {
      padding: 0;
      line-height: 0.62rem;
      height: 0.62rem;
      color: #2b2b2b;
      text-align: center;
    }
    .dateTable {
      border-bottom: 1px solid #ddd;
    }
    ul {
      display: flex;
      padding: 0 0.05rem;
      li {
        color: #2b2b2b;
        flex: 1;
        box-sizing: border-box;
        padding-top: 0.15rem;
        min-height: 0.9rem;
        margin: 0.07rem 0.08rem;
        text-align: center;
        border-radius: 5px;
      }
    }
    .rz {
      font-size: 0.24rem;
      line-height: 0.3rem;
    }
  }
  .comfire-box {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 0.18rem;
    background: #fff;
    .comfire {
      background: #4499ff;
      color: #fff;
      height: 0.84rem;
      line-height: 0.84rem;
      text-align: center;
      border-radius: 0.1rem;
      font-size: 0.3rem;
    }
  }
}
</style>



