<template>
  <div class="Book">
    <div class="banner" v-if="HotelInfo.imageurls">
      <div class="swiper-container" id="banner">
        <div class="swiper-wrapper">
          <div class="swiper-slide" v-for="(item,index) in HotelInfo.imageurls" :key="item.id">
            <img :src="item.path" alt="">
          </div>
        </div>
        <div class="swiper-pagination"></div>
      </div>
      <div class="text" v-if="HotelInfo.noticeEnable==0">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-gonggao"></use>
        </svg>
        <marquee scrollamount="4" direction="left">{{HotelInfo.bulletin}}</marquee>
      </div>
    </div>
    <div class="weui-cells information">
      <a class="weui-cell weui-cell_access" :href="'tel:'+HotelInfo.phone">
        <div class="weui-cell__hd">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-dianhuahaoma"></use>
          </svg>
        </div>
        <div class="weui-cell__bd">
          <p>{{HotelInfo.phone}}</p>
        </div>
        <div class="weui-cell__ft"></div>
      </a>
      <a class="weui-cell weui-cell_access" href="javascript:;" @click="mapfn">
        <div class="weui-cell__hd">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-weizhi"></use>
          </svg>
        </div>
        <div class="weui-cell__bd">
          <p>{{HotelInfo.address}}</p>
        </div>
        <div class="weui-cell__ft"></div>
      </a>
      <a class="weui-cell weui-cell_access details" href="javascript:;" @click="dialog1=!dialog1" v-if="HotelInfo.installations">
        <div class="weui-cell__hd">
          <svg class="icon" aria-hidden="true" v-for="item in HotelInfo.installations" :key="item.id">
            <use :xlink:href="'#'+item.iconUrl"></use>
          </svg>
        </div>
        <div class="weui-cell__bd">
        </div>
        <div class="weui-cell__ft">详情/设施</div>
      </a>
    </div>
    <div class="weui-flex timebox">
      <div class="weui-flex__item">
        <div class="placeholder" @click="dialog3=true">
          <span class="time">{{day1}}</span>
          <span>{{Today}}</span>
          <span class="fr">入住</span>
        </div>
      </div>
      <div class="weui-flex__item" @click="dialog3=true">
        <div class="placeholder">
          <span class="time">{{day2}}</span>
          <span>{{Tomorrow}}</span>
          <span class="fr">离店</span>
        </div>
      </div>
    </div>
    <div class="swiper-container activeRegion" v-if="activitylist.length!=0">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="item in activitylist" :key="item.id">
          <div class="weui-cell ">
            <div class="weui-cell__hd ">
              <img :src="item.path" />
            </div>
            <div class="weui-cell__bd ">
              <div class="title " v-if="item.activityType==1 ">
                <p>{{item.activityName}}</p>
                <span class="special ">特价专区</span>
              </div>
              <div class="title " v-if="item.activityType==2 ">
                <p>{{item.activityName}}</p>
                <span class="Hour ">钟点专区</span>
              </div>
              <div class="title " v-if="item.activityType==3 ">
                <p>{{item.activityName}}</p>
                <span class="Seckill ">秒杀专区</span>
              </div>
              <div class="title " v-if="item.activityType==4 ">
                <p>{{item.activityName}}</p>
                <span class="group ">团购专区</span>
              </div>
              <div class="newPrice clearfix " v-if="(item.activityType==1)||(item.activityType==3) ">
                <span>
                  <i>￥</i>{{item.activityPrice |money}}</span> 起
              </div>

              <div class="newPrice clearfix " v-if="item.activityType==2 ">
                {{item.checkInTime |toHourMinute}}小时/
                <span>
                  <i>￥</i>{{item.activityPrice |money}}</span> 起
              </div>
              <div class="newPrice clearfix " v-if="item.activityType==4 ">
                <span>
                  <i>￥</i>{{item.activityPrice |money}}</span> 起 /{{item.minPurchaseNumber}}间起订
              </div>
              <div class="tiem newtime " ref="newtime ">剩余:00天00时00分00秒</div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="weui-cells room-list " v-if="Categorydata.length!=0">
      <div class="weui-cell " v-for="(item,index) in Categorydata" :key="index " @click="opendetails(item.id) ">
        <div class="weui-cell__hd ">
          <img :src="item.images[0].path" />
        </div>
        <div class="weui-cell__bd ">
          <div class="title ">
            <p>{{item.name}}</p>
            <a class="weui-btn_mini weui-btn weui-btn_plain-primary " v-if="item.remnantRoomEnable==0 ">剩余{{item.spare}}间</a>
          </div>
          <div class="details ">
            <div class="fl ">
              <p class="equipment ">
                <template v-if="item.breakfastEnable==0 ">{{item.breakfastQuantity}}份早餐</template>
                <template v-if="item.breakfastEnable==1 ">无早餐</template>
                {{options[item.Bed]}} {{item.acreage}}m
                <sup>2</sup>
              </p>
              <p class="gray ">订单确认后不可取消</p>
              <p class="Price">
                <span class="newPrice ">
                  <i>￥</i>{{item.displayPrice |money}}
                </span>
                <span style="font-size: 0.28rem;color: #b2b2b2;">起</span>
              </p>
            </div>
            <a href="javascript:;" class="weui-btn_mini weui-btn weui-btn_plain-primary fr " @click.stop="bookingfn(item)">预订</a>

          </div>
        </div>
      </div>
    </div>
    <!-- 弹框 -->
    <!-- 详情/设施 -->
    <div class="downDialog ">
      <div class="mask " v-if="dialog1" @click="dialog1=false"></div>
      <transition name="dynamic ">
        <div class=" Hoteldetails Dialogbody " v-if="dialog1">
          <div class="downDialog-top ">
            <i class="weui-icon-cancel " @click="dialog1=false"></i>
            详情/设施
          </div>
          <div class="downDialog_content ">
            <div class="region ">
              <h4 class="title mt15 ">基础设施</h4>
              <div class="weui-flex ">
                <div class="weui-flex__item " v-for="item in HotelInfo.installations " :key="item.id ">
                  <div class="placeholder ">
                    <svg class="icon " aria-hidden="true ">
                      <use :xlink:href=" '#'+item.iconUrl "></use>
                    </svg>{{item.name}}</div>
                </div>
              </div>
            </div>
            <div class="region ">
              <h4 class="title ">酒店介绍</h4>
              <p class="explain ">{{HotelInfo.desc}}</p>
            </div>
          </div>
        </div>
      </transition>
    </div>
    <!-- 房型详情 -->
    <div class="downDialog ">
      <div class="mask " v-if="dialog2 " @click="dialog2=false"></div>
      <transition name="dynamic ">
        <div class=" Roomsdetails Dialogbody " v-if="dialog2 ">
          <div class="downDialog-top ">
            <i class="weui-icon-cancel " @click="dialog2=false "></i>
            房型详情
          </div>
          <div class="downDialog_content ">
            <div class="weui-dialog__bd ">
              <div class="swiper-container " id="Roomsswiper">
                <div class="swiper-wrapper ">
                  <div class="swiper-slide " v-for="(item,index) in Categorydetails.images " :key="index ">
                    <img :src="item.path " alt=" ">
                  </div>
                </div>
                <!-- Add Pagination -->
                <div class="swiper-pagination" id="Roomspagination"></div>
              </div>
              <div class="column ">
                <div class="weui-flex ">
                  <div class="weui-flex__item ">
                    <div class="placeholder ">早餐：
                      <template v-if="Categorydetails.breakfastEnable==0 ">{{Categorydetails.breakfastQuantity}}张早餐劵</template>
                      <template v-if="Categorydetails.breakfastEnable==1 ">无早餐</template>
                    </div>
                  </div>
                  <div class="weui-flex__item ">
                    <div class="placeholder ">上网：{{Internet[Categorydetails.Internet]}}</div>
                  </div>
                </div>
                <div class="weui-flex ">
                  <div class="weui-flex__item ">
                    <div class="placeholder ">床型：{{options[Categorydetails.Bed]}}
                      <template v-if="Categorydetails.text2&&Categorydetails.text3 ">{{Categorydetails.text2}}x{{Categorydetails.text3}}m </template>2张</div>
                  </div>
                  <div class="weui-flex__item ">
                    <div class="placeholder ">卫浴：{{bathroom[Categorydetails.bathroom]}}</div>
                  </div>
                </div>
                <div class="weui-flex ">
                  <div class="weui-flex__item ">
                    <div class="placeholder ">可住：
                      <template v-if="Categorydetails.people ">{{Categorydetails.people}}人</template>
                    </div>
                  </div>
                  <div class="weui-flex__item ">
                    <div class="placeholder ">窗户：{{Windows[Categorydetails.Window]}}</div>
                  </div>
                </div>
                <div class="weui-flex ">
                  <div class="weui-flex__item ">
                    <div class="placeholder ">面积：
                      <template v-if="Categorydetails.acreage ">{{Categorydetails.acreage}}m
                        <sup>2</sup>
                      </template>

                    </div>
                  </div>
                  <div class="weui-flex__item ">
                    <div class="placeholder ">吸烟：{{smoke[Categorydetails.smoke]}}</div>
                  </div>
                </div>
              </div>
              <div class="text ">
                <h4 class="title mt10 ">退订规则</h4>
                <p>不可取消 订单确认后，不可被取消/变更；如未入住，酒店将 扣除全额房费。如订单不确认将全额退款至您的付款账户。</p>
                <h4 class="title mt10 ">入住押金</h4>
                <p>该房型的入住押金为{{Categorydetails.deposit|money}}元，退房时押金将结算入住期间商品消 费后退还顾客。</p>
                <h4 class="title mt10 ">房间说明</h4>
                <p>{{Categorydetails.desc}}</p>
              </div>
            </div>
            <div class="weui-dialog__ft ">
              <div class="newPrice fl ">
                <i>￥</i>{{Categorydetails.displayPrice |money}}
              </div>
              <button class="weui-btn weui-btn_mini fr " @click="bookingfn(Categorydetails)">预订</button>
            </div>
          </div>
        </div>
      </transition>
    </div>
    <!-- 日历 -->
    <div class="downDialog ">
      <div class="mask " v-if="dialog3 " @click="dialog3=false"></div>
      <transition name="dynamic ">
        <div class=" calendarDialog Dialogbody " v-if="dialog3 ">
          <div class="downDialog-top ">
            <i class="weui-icon-cancel " @click="dialog3=false "></i>
            选择日期
          </div>
          <div class="downDialog_content ">
            <Calendar :Calendardata.sync='Calendardata'></Calendar>
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import Calendar from "@/components/Calendar";
import { homePage } from "@/assets/js/request.js";
import { DateFormat } from "@/assets/js/filters.js";
export default {
  data() {
    return {
      HotelInfo: "",
      activitylist: [],
      dialog1: false,
      dialog2: false,
      dialog3: false,
      abjda: "",
      timer: null,
      Categorydata: [],
      day1: "",
      day2: "",
      roomInTime: null,
      roomOutTime: null,
      Today: "今天",
      Tomorrow: "明天",
      //床型
      options: ["单人床", "双人床", "大房床"],
      Internet: ["WIFI", "宽带", "WIFI/宽带"],
      bathroom: ["独立", "公用"],
      Windows: ["有窗户", "无"],
      smoke: ["可吸烟", "否"],
      Categorydetails: "",
      Calendardata: "",
      page: 1,
      pages: 1,
      flag: true
    };
  },
  components: {
    Calendar
  },
  mounted() {
    var _this = this;
    if (this.$route.params.hotelId) {
      sessionStorage.setItem("hotelId", this.$route.params.hotelId);
    }
    this.$store.dispatch("getHotelInfo").then(() => {
      this.HotelInfo = this.$store.state.HotelInfo;
      document.title = this.HotelInfo.name;
      setTimeout(() => {
        new Swiper("#banner", {
          loop: true,
          // 如果需要分页器
          autoplay: 5000,
          pagination: "#banner .swiper-pagination"
        });
      }, 100);
    });
    this.day1 = this.GetDateStr(0, true);
    this.day2 = this.GetDateStr(1, true);
    this.roomInTime =
      this.GetDateStr(0)
        .replace("年", "-")
        .replace("月", "-")
        .replace("日", "") + " 00:00:00";
    this.roomOutTime =
      this.GetDateStr(1)
        .replace("年", "-")
        .replace("月", "-")
        .replace("日", "") + " 00:00:00";
    sessionStorage.setItem("storeInTime", this.GetDateStr(0));
    sessionStorage.setItem("storeOutTime", this.GetDateStr(1));
    //拿活动列表数据
    homePage
      .activitylist({ Suffix: sessionStorage.getItem("hotelId") + "/activity" })
      .then(res => {
        if (res.code == 0) {
          var _this = this;
          if (res.data.records.length != 0) {
            this.activitylist = res.data.records;
            setTimeout(() => {
              var activeRegionSwiper = new Swiper(".activeRegion", {
                slidesPerView: "auto",
                centeredSlides: true,
                paginationClickable: true,
                spaceBetween: 10,
                loop: true,
                // autoplay: 5000,
                autoplayDisableOnInteraction: false,
                onSlideChangeStart: function(swiper) {
                  clearInterval(_this.timer);
                  _this.countDown(
                    _this.activitylist[
                      swiper.activeIndex % _this.activitylist.length
                    ].endTime
                  );
                }
              });
              activeRegionSwiper.on("click", function(swiper) {
                var item =
                  _this.activitylist[
                    swiper.activeIndex % _this.activitylist.length
                  ];
                _this.$router.push(
                  "/book/activitydetails/" + item.activityType + "/" + item.id
                );
              });
            }, 100);
          }
        }
      });
    // homePage.activitylist({
    //   Suffix: sessionStorage.getItem("hotelId") + "/activity",
    //   fn: data => {
    //     var _this = this;
    //     if (data.records.length != 0) {
    //       this.activitylist = data.records;
    //       setTimeout(() => {
    //         var activeRegionSwiper = new Swiper(".activeRegion", {
    //           slidesPerView: "auto",
    //           centeredSlides: true,
    //           paginationClickable: true,
    //           spaceBetween: 10,
    //           loop: true,
    //           // autoplay: 5000,
    //           autoplayDisableOnInteraction: false,
    //           onSlideChangeStart: function(swiper) {
    //             clearInterval(_this.timer);
    //             _this.countDown(
    //               _this.activitylist[
    //                 swiper.activeIndex % _this.activitylist.length
    //               ].endTime
    //             );
    //           }
    //         });
    //         activeRegionSwiper.on("click", function(swiper) {
    //           var item =
    //             _this.activitylist[
    //               swiper.activeIndex % _this.activitylist.length
    //             ];
    //           _this.$router.push(
    //             "/book/activitydetails/" + item.activityType + "/" + item.id
    //           );
    //         });
    //       }, 100);
    //     }
    //   }
    // });
    //拿首页房型列表
    this.getRoomCategory();
    //获取滚动条当前的位置
    function getScrollTop() {
      var scrollTop = 0;
      if (document.documentElement && document.documentElement.scrollTop) {
        scrollTop = document.documentElement.scrollTop;
      } else if (document.body) {
        scrollTop = document.body.scrollTop;
      }
      return scrollTop;
    }
    //获取当前可是范围的高度
    function getClientHeight() {
      var clientHeight = 0;
      if (document.body.clientHeight && document.documentElement.clientHeight) {
        clientHeight = Math.min(
          document.body.clientHeight,
          document.documentElement.clientHeight
        );
      } else {
        clientHeight = Math.max(
          document.body.clientHeight,
          document.documentElement.clientHeight
        );
      }
      return clientHeight;
    }
    //获取文档完整的高度
    function getScrollHeight() {
      return Math.max(
        document.body.scrollHeight,
        document.documentElement.scrollHeight
      );
    }
    window.onscroll = function() {
      if (
        getScrollTop() + getClientHeight() > getScrollHeight() - 50 &&
        _this.flag
      ) {
        _this.page++;
        if (_this.page > _this.pages) {
          _this.Toast({
            message: "已加载所有数据",
            duration: 2000
          });
          _this.flag = false;
          return false;
        }
        _this.getRoomCategory();
      }
    };
  },
  watch: {
    Calendardata: function(val, oldVal) {
      if (val) {
        this.roomInTime =
          val[0]
            .replace("年", "-")
            .replace("月", "-")
            .replace("日", "") + " 00:00:00";
        this.roomOutTime =
          val[1]
            .replace("年", "-")
            .replace("月", "-")
            .replace("日", "") + " 00:00:00";
        this.flag = true;
        this.getRoomCategory();
        if (this.GetDateStr(0) == val[0]) {
          this.Today = "今天";
        } else if (this.GetDateStr(1) == val[0]) {
          this.Today = "明天";
        } else {
          switch (new Date(this.roomInTime).getDay()) {
            case 0:
              this.Today = "星期日";
              break;
            case 1:
              this.Today = "星期一";
              break;
            case 2:
              this.Today = "星期二";
              break;
            case 3:
              this.Today = "星期三";
              break;
            case 4:
              this.Today = "星期四";
              break;
            case 5:
              this.Today = "星期五";
              break;
            case 6:
              this.Today = "星期六";
              break;
          }
        }
        if (this.GetDateStr(0) == val[1]) {
          this.Tomorrow = "今天";
        } else if (this.GetDateStr(1) == val[1]) {
          this.Tomorrow = "明天";
        } else {
          switch (new Date(this.roomOutTime).getDay()) {
            case 0:
              this.Tomorrow = "星期日";
              break;
            case 1:
              this.Tomorrow = "星期一";
              break;
            case 2:
              this.Tomorrow = "星期二";
              break;
            case 3:
              this.Tomorrow = "星期三";
              break;
            case 4:
              this.Tomorrow = "星期四";
              break;
            case 5:
              this.Tomorrow = "星期五";
              break;
            case 6:
              this.Tomorrow = "星期六";
              break;
          }
        }
        this.day1 = val[0].slice(5, val[0].length);
        this.day2 = val[1].slice(5, val[0].length);
        sessionStorage.setItem("storeInTime", val[0]);
        sessionStorage.setItem("storeOutTime", val[1]);
      }
      this.dialog3 = false;
    }
  },
  methods: {
    countDown(val) {
      var _this = this;
      var abc = function(params) {
        var utc = (val - new Date().getTime()) / 1000;
        if (utc <= 0) {
          var doms = document.getElementsByClassName("newtime");
          for (var i = 0; i < doms.length; i++) {
            doms[i].innerHTML =
              "剩余: " + "00" + "天" + "00" + "时" + "00" + "分" + "00" + "秒";
          }
        } else {
          var tian = Math.floor(utc / (24 * 60 * 60));
          var shi = Math.floor((utc / 60 / 60) % 24);
          var fen = Math.floor((utc / 60) % 60);
          var miao = Math.floor(utc % 60);
          if (tian < 10) {
            tian = "0" + tian;
          }
          if (shi < 10) {
            shi = "0" + shi;
          }
          if (fen < 10) {
            fen = "0" + fen;
          }
          if (miao < 10) {
            miao = "0" + miao;
          }
          var doms = document.getElementsByClassName("newtime");
          for (var i = 0; i < doms.length; i++) {
            doms[i].innerHTML =
              "剩余: " + tian + "天" + shi + "时" + fen + "分" + miao + "秒";
          }
        }
      };
      abc();
      _this.timer = setInterval(function() {
        abc();
      }, 1000);
    },
    //查看房间详情
    opendetails(id) {
      this.Categorydetails = "";
      this.Categorydata.forEach(function(item) {
        if (item.id == id) {
          this.Categorydetails = item;
        }
      }, this);
      this.dialog2 = true;
      this.Categorydetails.bathroom = JSON.parse(
        this.Categorydetails.infrastructureRelations[4].displayValue
      ).bathroom;
      this.Categorydetails.Bed = JSON.parse(
        this.Categorydetails.infrastructureRelations[0].displayValue
      ).text1;
      this.Categorydetails.text2 = JSON.parse(
        this.Categorydetails.infrastructureRelations[0].displayValue
      ).text2;
      this.Categorydetails.text3 = JSON.parse(
        this.Categorydetails.infrastructureRelations[0].displayValue
      ).text3;
      this.Categorydetails.text4 = JSON.parse(
        this.Categorydetails.infrastructureRelations[0].displayValue
      ).text4;
      this.Categorydetails.Internet = JSON.parse(
        this.Categorydetails.infrastructureRelations[3].displayValue
      ).Internet;
      this.Categorydetails.people = JSON.parse(
        this.Categorydetails.infrastructureRelations[2].displayValue
      ).people;
      this.Categorydetails.Window = JSON.parse(
        this.Categorydetails.infrastructureRelations[5].displayValue
      ).Window;
      this.Categorydetails.acreage = JSON.parse(
        this.Categorydetails.infrastructureRelations[1].displayValue
      ).acreage;
      this.Categorydetails.smoke = JSON.parse(
        this.Categorydetails.infrastructureRelations[6].displayValue
      ).smoke;
      this.$nextTick(() => {
        new Swiper("#Roomsswiper", {
          pagination: "#Roomspagination",
          paginationType: "fraction",
          loop: true
        });
      });
    },
    //拿首页房型列表
    getRoomCategory() {
      if (this.flag) {
        this.flag = false;
        homePage
          .roomCategory({
            Suffix: sessionStorage.getItem("hotelId") + "/roomCategory",
            params: {
              hotelId: sessionStorage.getItem("hotelId"),
              roomInTime: this.roomInTime,
              roomOutTime: this.roomOutTime,
              page: this.page
            }
          })
          .then(res => {
            if (res.code == 0) {
              if (res.data.records.length != 0) {
                this.Categorydata = res.data.records;
                this.Categorydata.forEach(function(item) {
                  item.Bed = JSON.parse(
                    item.infrastructureRelations[0].displayValue
                  ).text1;
                  item.acreage = JSON.parse(
                    item.infrastructureRelations[1].displayValue
                  ).acreage;
                  item.spare = item.roomCount - item.orderCount;
                }, this);
              }
              this.pages = res.data.pages;
            }
            this.flag = true;
          });
        // homePage.roomCategory({
        //   Suffix: sessionStorage.getItem("hotelId") + "/roomCategory",
        //   params: {
        //     hotelId: sessionStorage.getItem("hotelId"),
        //     roomInTime: this.roomInTime,
        //     roomOutTime: this.roomOutTime,
        //     page: this.page
        //   },
        //   fn: data => {
        //     if (data.records.length != 0) {
        //       this.Categorydata = data.records;
        //       this.Categorydata.forEach(function(item) {
        //         item.Bed = JSON.parse(
        //           item.infrastructureRelations[0].displayValue
        //         ).text1;
        //         item.acreage = JSON.parse(
        //           item.infrastructureRelations[1].displayValue
        //         ).acreage;
        //         item.spare = item.roomCount - item.orderCount;
        //       }, this);
        //     }
        //     this.flag = true;
        //     this.pages = data.pages;
        //   }
        // });
      }
    },
    GetDateStr(AddDayCount, fayg) {
      var dd = new Date();
      dd.setDate(dd.getDate() + AddDayCount); //获取AddDayCount天后的日期
      var y = dd.getFullYear();
      var m = dd.getMonth() + 1; //获取当前月份的日期
      var d = dd.getDate();
      if (m < 10) {
        m = "0" + m;
      }
      if (d < 10) {
        d = "0" + d;
      }
      if (fayg) {
        return m + "月" + d + "日";
      } else {
        return y + "年" + m + "月" + d + "日";
      }
    },
    mapfn() {
      location.href =
        "http://apis.map.qq.com/tools/routeplan/eword=" +
        this.HotelInfo.address +
        "&epointx=" +
        this.HotelInfo.longitude +
        "&epointy=" +
        this.HotelInfo.latitude +
        "?referer=mapqq&key=GQKBZ-BKZW6-ZCVSQ-MD5GX-MYNBQ-LSBPK";
    },
    bookingfn(item) {
      sessionStorage.setItem("storeRoom", JSON.stringify(item));
      this.$router.push("/bookImmediately/" + item.id);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style   lang='less' rel='stylesheet/less'>
/* 可以设置不同的进入和离开动画 */

/* 设置持续时间和动画函数 */

.dynamic-enter-active {
  transition: all 0.5s ease;
}

.dynamic-leave-active {
  transition: all 0.5s ease;
}

.dynamic-enter,
.dynamic-leave-to {
  opacity: 0;
  transform: translateY(100%);
}

.mask {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 3;
}

.Calendar-mask {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 4;
}
</style>



