import $ from 'jquery';
//默认值
var defaults = {
  selectors: {
    sections: '#calendar'
  },
  index: 4,
  //展示的月份个数
  animateFunction: 'toggle',
  //动画效果
  controlDay: false,
  //知否控制在daysnumber天之内，这个数值的设置前提是总显示天数大于90天
  daysnumber: '90',
  //控制天数
  comeColor: 'blue',
  //入住颜色
  outColor: 'red',
  //离店颜色
  comeoutColor: '#0cf',
  //入住和离店之间的颜色
  callback: '',
  //回调函数
  comfireBtn: '.comfire' //确定按钮的class或者id
};
function calendarSwitch(options) {
  this.settings = $.extend(true, defaults, options || {});
  this.init();
}
calendarSwitch.prototype = {
  /*说明：初始化插件*/
  /*实现：初始化dom结构，布局，分页及绑定事件*/
  init: function() {
    var me = this;
    me.selectors = me.settings.selectors;
    me.sections = me.selectors.sections;
    me.index = me.settings.index;
    me.comfire = me.settings.comfireBtn;
    me.callback = me.settings.callback;
    var html =
      "<table class='dateZone'><tr><td class='colo'>日</td><td>一</td><td>二</td><td>三</td><td>四</td><td>五</td><td class='colo'>六</td></tr></table>" +
      "<div class='tbody'></div><div class='comfire-box'><div class='comfire'>确定</div></div>";
    $(me.sections).append(html);
    $(me.sections)
      .find('.headerWrapper')
      .css({
        height: '50px',
        'line-height': '50px',
        position: 'relative'
      });
    $(me.sections)
      .find('.headerTip')
      .css({
        'text-align': 'center',
        'line-height': '50px'
      });
    for (var q = 0; q < me.index; q++) {
      var select = q;
      $(me.sections)
        .find('.tbody')
        .append("<p class='ny1'></p><div class='dateTable'></div>");
      var currentDate = new Date();
      currentDate.setMonth(currentDate.getMonth() + select);
      var currentYear = currentDate.getFullYear();
      var currentMonth = currentDate.getMonth();
      var setcurrentDate = new Date(currentYear, currentMonth, 1);
      var firstDay = setcurrentDate.getDay();
      var yf = currentMonth + 1;
      if (yf < 10) {
        $(me.sections)
          .find('.ny1')
          .eq(select)
          .text(currentYear + '年' + '0' + yf + '月');
      } else {
        $(me.sections)
          .find('.ny1')
          .eq(select)
          .text(currentYear + '年' + yf + '月');
      }
      var DaysInMonth = [];
      if (me._isLeapYear(currentYear)) {
        DaysInMonth = new Array(31, 29, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31);
      } else {
        DaysInMonth = new Array(31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31);
      }
      var Ntd = firstDay + DaysInMonth[currentMonth];
      var Ntr = Math.ceil(Ntd / 7);
      for (var i = 0; i < Ntr; i++) {
        $(me.sections)
          .find('.dateTable')
          .eq(select)
          .append('<ul></ul>');
      }
      var createTd = $(me.sections)
        .find('.dateTable')
        .eq(select)
        .find('ul');
      createTd.each(function(index, element) {
        for (var j = 0; j < 7; j++) {
          $(this).append('<li></li>');
        }
      });
      var arryTd = $(me.sections)
        .find('.dateTable')
        .eq(select)
        .find('li');
      for (var m = 0; m < DaysInMonth[currentMonth]; m++) {
        arryTd.eq(firstDay++).text(m + 1);
      }
    }
    me._initselected();
    $(me.comfire).on('click', function(event) {
      event.preventDefault();
      if ($('.ruzhu').length == 0) {
        return false;
      }
      if ($('.lidian').length == 0) {
        return false;
      }
      var ruzhuday = $('.ruzhu')
        .html()
        .split('<')[0];
      var lidianday = $('.lidian')
        .html()
        .split('<')[0];
      var ruzhunian = $('.ruzhu')
        .parents('.dateTable')
        .prev('.ny1')
        .html();
      var lidiannian = $('.lidian')
        .parents('.dateTable')
        .prev('.ny1')
        .html();
      if (ruzhuday < 10) {
        ruzhuday = '0' + ruzhuday;
      }
      if (lidianday < 10) {
        lidianday = '0' + lidianday;
      }
      var ruzhutime = ruzhunian + ruzhuday + '日';
      var lidiantime = lidiannian + lidianday + '日';
      var choiceDay = [];
      choiceDay.push(ruzhutime);
      choiceDay.push(lidiantime);
      me.callback(choiceDay);
    });
  },
  _isLeapYear: function(year) {
    return year % 4 == 0 && (year % 100 != 0 || year % 400 == 0);
  },
  _slider: function(id) {
    var me = this;
    me.animateFunction = me.settings.animateFunction;
    if (me.animateFunction == 'fadeToggle') {
      $(id).fadeToggle();
    } else if (me.animateFunction == 'slideToggle') {
      $(id).slideToggle();
    } else if (me.animateFunction == 'toggle') {
      $(id).toggle();
    }
  },
  _initselected: function() {
    var me = this;
    me.comeColor = me.settings.comeColor;
    me.outColor = me.settings.outColor;
    me.daysnumber = me.settings.daysnumber;
    var strDays = new Date().getDate();
    var arry = [];
    var arry1 = [];
    var ruzhuMonth = [];
    var lidianMonth = [];
    $('.tbody')
      .find('li')
      .removeClass('ruzhu');
    $('.tbody')
      .find('li')
      .removeClass('lidian');
    var tds = $(me.sections)
      .find('.dateTable')
      .eq(0)
      .find('li');
    $(me.sections)
      .find('.ny1')
      .each((i, item) => {
        if (
          $(item).text() == sessionStorage.getItem('storeInTime').slice(0, 8)
        ) {
          ruzhuMonth = $(item)
            .next('.dateTable')
            .find('li');
        }
        if (
          $(item).text() == sessionStorage.getItem('storeOutTime').slice(0, 8)
        ) {
          lidianMonth = $(item)
            .next('.dateTable')
            .find('li');
        }
      });
    ruzhuMonth.each(function(index, element) {
      var newday = parseInt($(this).text());
      if (newday < 10) {
        newday = '0' + newday;
      }
      if (newday == sessionStorage.getItem('storeInTime').slice(8, -1)) {
        $(this).addClass('ruzhu');
        $(this).append('<p class="rz">入住</p>');
        me._checkColor(me.comeColor, me.outColor);
      }
    });
    lidianMonth.each(function(index, element) {
      var newday = parseInt($(this).text());
      if (newday < 10) {
        newday = '0' + newday;
      }
      if (newday == sessionStorage.getItem('storeOutTime').slice(8, -1)) {
        $(this).addClass('lidian');
        $(this).append('<p class="rz">离店</p>');
        me._checkColor(me.comeColor, me.outColor);
      }
    });

    // tds.each(function (index, element) {

    //     if ($(this).text() == strDays) {
    //         var r = index;
    //         $('.tbody').find('li').removeClass('ruzhu');
    //         $('.tbody').find('li').removeClass('lidian');
    //         $(this).addClass('ruzhu');
    //         $(this).append('</br><p class="rz">入住</p>');
    //         if ($(this).next().text() != "") {
    //             $(this).next().addClass('lidian');
    //             $(this).next().append('</br><p class="rz">离店</p>');
    //         } else {
    //             $(".dateTable").eq(1).find("li").each(function (index, el) {
    //                 if ($(this).text() != "") {
    //                     $(this).addClass('lidian');
    //                     $(this).append('</br><p class="rz">离店</p>');
    //                     return false;
    //                 }
    //             });
    //         }
    //         me._checkColor(me.comeColor, me.outColor)

    //     }
    // })

    $(me.sections)
      .find('.tbody')
      .find('li')
      .each(function(index, element) {
        if ($(this).text() != '') {
          arry.push(element);
        }
      });
    for (var i = 0; i < strDays - 1; i++) {
      $(arry[i]).css('color', '#b2b2b2');
    }
    if (me.daysnumber) {
      //可以在这里添加90天的条件

      for (var i = strDays - 1; i < strDays + parseInt(me.daysnumber); i++) {
        arry1.push(arry[i]);
      }
      for (var i = strDays + parseInt(me.daysnumber); i < $(arry).length; i++) {
        $(arry[i]).css('color', '#b2b2b2');
      }
    } else {
      for (var i = strDays - 1; i < $(arry).length; i++) {
        arry1.push(arry[i]);
      }
    }
    me._selectDate(arry1);
  },
  _checkColor: function(comeColor, outColor) {
    var me = this;
    var rz = $(me.sections).find('.rz');
    for (var i = 0; i < rz.length; i++) {
      if (rz.eq(i).text() == '入住') {
        rz
          .eq(i)
          .closest('li')
          .css({
            background: comeColor,
            color: '#fff'
          });
      } else {
        rz
          .eq(i)
          .closest('li')
          .css({
            background: outColor,
            color: '#fff'
          });
      }
    }
  },
  _callback: function() {
    var me = this;
    if (me.settings.callback && $.type(me.settings.callback) === 'function') {
      me.settings.callback();
    }
  },
  _selectDate: function(arry1) {
    var me = this;
    me.comeColor = me.settings.comeColor;
    me.outColor = me.settings.outColor;
    me.comeoutColor = me.settings.comeoutColor;
    me.sections = me.selectors.sections;

    var flag = 0;
    var first;
    var sum;
    var second;
    var startDay;
    var endDay;
    $(arry1).on('click', function(index) {
      //第一次点击
      if (flag == 0) {
        $(me.sections)
          .find('.hover')
          .remove();

        $(me.sections)
          .find('.tbody')
          .find('p')
          .remove('.rz');
        $(me.sections)
          .find('.tbody')
          .find('br')
          .remove();
        $(arry1).css({
          background: '#fff',
          color: '#000'
        });
        $('.tbody')
          .find('li')
          .removeClass('ruzhu');
        $('.tbody')
          .find('li')
          .removeClass('lidian');
        $(this).addClass('ruzhu');
        $(this).append('<p class="rz">入住</p>');
        first = $(arry1).index($(this));
        me._checkColor(me.comeColor, me.outColor);
        flag = 1;
      } else if (flag == 1) {
        //第二次点击
        flag = 0;

        second = $(arry1).index($(this));
        sum = Math.abs(second - first);
        if (sum == 0) {
          sum = 1;
        }

        if (first < second) {
          $('.tbody')
            .find('li')
            .removeClass('lidian');
          $(this).append('<p class="rz">离店</p>');
          $(this).addClass('lidian');
          first = first + 1;
          for (first; first < second; first++) {
            $(arry1[first]).css({
              background: me.comeoutColor,
              color: '#51a0ff'
            });
          }
        } else if (first == second) {
          // $('.tbody')
          //   .find('li')
          //   .removeClass('ruzhu');
          // $('.tbody')
          //   .find('li')
          //   .removeClass('lidian');
          // $(this).addClass('ruzhu');
          // $(this).addClass('lidian');
          // $(me.sections)
          //   .find('.rz')
          //   .text('入住');
          // $(this).append('<p class="rz">离店</p>');
          // $(this)
          //   .find('.rz')
          //   .css('font-size', '12px');
          // var e = $(this)
          //   .text()
          //   .replace(/[^0-9]/gi, '');
          // var c, d;
          // var a = new Array();
          // var b = new Array();
          // var f;
          // var same = $(this)
          //   .parents('.dateTable')
          //   .prev('p')
          //   .text()
          //   .replace(/[^0-9]/gi, '')
          //   .split('');
          // for (var i = 0; i < 4; i++) {
          //   a.push(same[i]);
          // }
          // c = a.join('');
          // for (var j = 4; j < 6; j++) {
          //   b.push(same[j]);
          // }
          // d = b.join('');
          // f = c + '-' + d + '-' + e;
        } else if (first > second) {
          $('.tbody')
            .find('li')
            .removeClass('ruzhu');
          $('.tbody')
            .find('li')
            .removeClass('lidian');
          $(me.sections)
            .find('.rz')
            .parents('li')
            .addClass('lidian');
          $(this).addClass('ruzhu');
          $(me.sections)
            .find('.rz')
            .text('离店');
          $(this).append('<p class="rz">入住</p>');
          second = second + 1;
          for (second; second < first; second++) {
            $(arry1[second]).css({
              background: me.comeoutColor,
              color: '#fff'
            });
          }
        }
        $(me.sections)
          .find('.rz')
          .each(function(index, element) {
            if ($(this).text() == '离店') {
              $(this)
                .parent('td')
                .append('<span class="hover">' + sum + '天</span>');
              $(this)
                .parent('td')
                .css('position', 'relative');
            }
          });

        $('.hover').css({
          position: 'absolute',
          left: '-17px',
          top: '0px'
        });
        me._slider('firstSelect');

        //点击的日期存入input
        $(me.sections)
          .find('.tbody .rz')
          .each(function(index, element) {
            if ($(this).text() == '入住') {
              var day = parseInt(
                $(this)
                  .parent()
                  .text()
                  .replace(/[^0-9]/gi, '')
              ); //截取字符串中的数字
              startDay =
                $(this)
                  .parents('.dateTable')
                  .prev('p')
                  .text() +
                parseInt(
                  $(this)
                    .parent()
                    .text()
                    .replace(/[^0-9]/gi, '')
                    .substring(0, 2)
                );
            }
            if ($(this).text() == '离店') {
              startDay =
                $(this)
                  .parents('.dateTable')
                  .prev('p')
                  .text() +
                parseInt(
                  $(this)
                    .parent()
                    .text()
                    .replace(/[^0-9]/gi, '')
                    .substring(0, 2)
                );
            }
          });
        var myweek = [
          '星期日',
          '星期一',
          '星期二',
          '星期三',
          '星期四',
          '星期五',
          '星期六'
        ];

        // var st = new Date($('#startDate').val());
        // var en = new Date($('#endDate').val());
        // $('.week').text(myweek[st.getDay()])
        // $('.week1').text(myweek[en.getDay()])
        me._checkColor(me.comeColor, me.outColor);
      }
      //第二次点击结束
    });
  }
};
export default calendarSwitch;



